function hex(color): string {
  return "#" + componentToHex(color[0]) + componentToHex(color[1]) + componentToHex(color[2]);
}
function componentToHex(c): string {
  var hex = c.toString(16);
  return hex.length == 1 ? "0" + hex : hex;
}

