function App() {
  const [state, setState] = useState<State>({})
  const [colors, setColors] = useState<any>([])
  const [colorCount, setColorCount] = useState(8)
  const [activeColor, setActiveColor] = useState(null)
  const canvas = useRef<HTMLCanvasElement | null>(null)
  const onImageLoaded = useCallback((image) => {
    if (canvas.current) {
      setState({ img: image })
      setColors([])
      const ctx = canvas.current.getContext('2d') as CanvasRenderingContext2D;
      ctx.resetTransform()
      ctx.clearRect(0, 0, canvas.current.width, canvas.current.height)
      canvas.current.height = 600
      canvas.current.width = 600
      let s = 1
      if (image.width >= image.height) {
        s = canvas.current.width / image.width
        ctx.scale(s, s)
      }
      else {
        s = canvas.current.height / image.height
        ctx.scale(s, s)
        ctx.translate(image.width / 2, 0)
      }
      ctx.drawImage(image, 0, 0, image.width, image.height)
    }
  }, [])
  const onConvert = useCallback(() => {
    console.log(state)
    if (state.img) {
      const data = htmlImageToImageData(state.img)
      // console.log(data)
      // const pal = MMCQTS.quantize(data, 4)
      const pal = RgbQuant.palette(data, { colors: colorCount })
      const hexs = pal.map(c => hex(c))
      const uniqueArray = hexs.filter(function (item, pos) {
        return hexs.indexOf(item) == pos;
      })
      console.log(uniqueArray)
      setColors(uniqueArray)
    }
  }, [state, colorCount])
  const onChangeColorCount = useCallback((e) => {
    console.log(e.target.value)
    setColorCount(e.target.value)
  }, [])
  const onClickColor = useCallback((e) => {
    window.scrollTo(0, document.body.scrollHeight);
    setActiveColor(e)
  }, [])
  return (
    <MainWrap>
      <Main >
        <Intro>Extract desired number of colors from your image! No image-upload, processing takes place locally in the browser.</Intro>
        <ImageFileButton imageLoaded={onImageLoaded} />
        <Canvas ref={canvas} />
        {state.img &&
          <ConvertWrap>
            <ConvertButton onConvert={onConvert} />
            <ColorCount number={colorCount} onChange={onChangeColorCount} />
          </ConvertWrap>
        }
        {state.img &&
          <ColorWrap>
            {colors.map(c => <Color key={c} color={c} onClick={onClickColor} />)}
          </ColorWrap>
        }
      </Main>
      <Bottom />
      <Footer />
    </MainWrap>
  );
}

export default App;
